.characters-page {
  padding: 73px 157px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.char-title {
  font-family: "Karla";
  font-style: normal;
  font-weight: 600;
  font-size: 35px;
  line-height: 41px;
  text-align: center;
  letter-spacing: 3px;
  color: #000000;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.char-search {
  margin-top: 36px;
  padding: 18px 896px 18px 31px;
  background: #f2f2f2;
  border: none;
  box-shadow: 4px 4px 16px rgba(1, 28, 64, 0.2);
  border-radius: 40px;
  font-family: "Source Sans Pro";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: rgba(33, 33, 33, 0.8);
}

.char-list {
  margin-top: 110px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  row-gap: 45px;
  column-gap: 34px;
}

.button-wrapper{
  margin-top: 30px;
  display: flex;
  column-gap: 100px;
}
